export default{
	data(){
		return{
			Msglist:[],
			userInfo:{},			
			
		}
	},
	onShow(){
		let userInfo=uni.getStorageSync("userInfo")
		if(userInfo){
			this.userInfo=JSON.parse(userInfo)
		}
		this.getMsgList()
	},
	methods:{
		goDetails(){
			uni.navigateTo({
				url:"./info"
			})
		},
		getMsgList(){
			this.$http.post("sMessage/list",{
				id:this.userInfo.id,
				type:2
			}).then(res=>{
				if(res.code==100){
					let list=res.info
					list.forEach((item,index)=>{
						item.createTime=this.$untils.getDate(item.createTime)
					})
					this.Msglist=list
				}
			})
		},
	}
}
